.bg-admin {
  background-color: #F5F7FB;
  min-height: 100vh;
}

.bg-admin .sidebar {
  min-height: 100vh;
}

.bg-admin .sidebar .menuItem {
  cursor: pointer;
}

.bg-admin .sidebar .signoutLink {
  position: fixed;
  bottom: 20px;
}

@media (max-width: 768px) {
  .bg-admin .sidebar {
    min-height: inherit;
  }
  .bg-admin .sidebar .signoutLink {
    position: inherit;
    margin-top: 30px;
    margin-bottom: 10px;
  }
}

.bg-admin .admin-dashboard i {
  color: #837185;
}

.bg-admin .admin-dashboard .table-customer-store-statistics table tr th {
  padding-top: 0;
}

.bg-admin .admin-dashboard .table-customer-store-statistics table tr td {
  padding-top: 2px;
  padding-bottom: 2px;
}

.bg-admin .admin-dashboard .table-customer-store-statistics table tbody tr .have-length {
  min-width: 160px;
}

.bg-admin .admin-dashboard .table-customer-store-statistics table { 
  border-collapse: separate; 
  border-spacing: 0 10px; 
  margin-top: -10px; /* correct offset on first border spacing if desired */
}

.bg-admin .admin-dashboard .table-customer-store-statistics td {
  border: solid 1px #ddd !important;
  border-style: solid none !important;
}

.bg-admin .admin-dashboard .table-customer-store-statistics td:first-child {
  border-left-style: solid !important;
  border-top-left-radius: 10px !important; 
  border-bottom-left-radius: 10px !important;
}

.bg-admin .admin-dashboard .table-customer-store-statistics td:last-child {
  border-right-style: solid !important;
  border-bottom-right-radius: 10px !important; 
  border-top-right-radius: 10px !important; 
}